import React from 'react';

import './App.css';
import {
    BrowserRouter as Router,
    Route
}from 'react-router-dom';

import Home from './components/home';  // 导入Expert组件
import Index from './components/expertlib';  // 导入Expert组件
import Search from './components/searchlib'; // Search
import Detail from './components/detail'; // Search


function App() {
  return (
      <Router>
          <div>
              <Route exact path="/" component={Home} />
              <Route exact path="/expert" component={Index} />
              <Route path="/search" component={Search} />
              <Route path="/detail" component={Detail} />
          </div>
      </Router>
  );
}

export default App;
